/******************************************************************

.p-introduction

******************************************************************/
.p-introduction{
  padding-top: rem(50);
  padding-bottom: rem(50);
  @include mq('tab'){
    padding-top: rem(70);
    padding-bottom: rem(70);
  }
}

// 画像
.p-introduction__img{
  position: relative;
  overflow: hidden;
}

.p-introduction__img::before{
  content: '';
  display: block;
  padding-top: 75%;
}

.p-introduction__img img{
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.p-introduction__line{
  margin-top: rem(30);
  border-top: rem(1) solid $gray;

  @include mq('tab'){
    margin-top: rem(40);
  }
}

.p-introduction__contents{
  margin-top: rem(30);

  @include mq('tab'){
    margin-top: rem(40);
  }
}

.p-introduction__url > a{
  font-size: rem(14);
  line-height: 1.5;
  color: $link;
  @include mq('tab'){
    font-size: rem(16);
  }
}

.p-introduction__user{
  margin-top: rem(5);
  font-size: rem(14);
  line-height: 1.5;
  color: $text-color;
  @include mq('tab'){
    font-size: rem(16);
  }
}

.p-introduction__password{
  margin-top: rem(5);
  font-size: rem(14);
  line-height: 1.5;
  color: $text-color;
  @include mq('tab'){
    font-size: rem(16);
  }
}

.p-introduction__comment{
  margin-top: rem(20);
  font-size: rem(14);
  line-height: 1.5;
  color: $text-color;
  @include mq('tab'){
    font-size: rem(16);
  }
}

.p-introduction__list{
  margin-top: rem(30);
  border-top: rem(1) solid $gray;

  @include mq('tab'){
    margin-top: rem(40);
  }
}

.p-introduction-list__block{
  display: flex;
  align-items: flex-start;
  border-bottom: rem(1) solid $gray;
}

.p-introduction-list__title{
  width: 30%;
  padding-top: rem(15);
  padding-bottom: rem(15);
  font-size: rem(14);
  line-height: 1.5;
  color: $text-color;
  @include mq('tab'){
    font-size: rem(16);
  }
}

.p-introduction-list__detail{
  flex: 1;
  padding-top: rem(15);
  padding-bottom: rem(15);
  font-size: rem(14);
  line-height: 1.5;
  color: $text-color;
  @include mq('tab'){
    font-size: rem(16);
  }
}


// ページネーション
.p-introduction__pagination{
  margin-top: rem(80);
  margin-right: auto;
  margin-left: auto;
  width: 100%;
  max-width: rem(500);
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  
  @include mq('tab'){
    margin-top: rem(120);
  }
}

// 前へ prev
.p-introduction__previous a{
  padding: rem(10);
  width: rem(100);
  display: inline-block;
  font-family: $font-family-sub;
  font-size: rem(14);
  line-height: 1;
  letter-spacing: 0.02em;
  border-bottom: rem(1) solid $text-color;
  text-align: right;
  text-transform: uppercase;
  position: relative;

  @include mq('tab'){
    width: rem(130);
    font-size: rem(16);
  }
}

.p-introduction__previous a::after{
  content: '';
  display: inline-block;
  width: rem(7);
  height: rem(7);
  border-bottom: rem(2) solid $text-color;
  border-left: rem(2) solid $text-color;
  position: absolute;
  top: 50%;
  left: rem(10);

  transform-origin: center;
  transform: rotate(45deg) translate(0, -50%);
  transition: left .3s;
}

.p-introduction__previous a:hover::after{
  left: rem(5);
}

// 一覧へ
.p-introduction__archive{
  padding: rem(10);
  display: inline-block;
  font-size: rem(14);
  line-height: 1;
  border-bottom: rem(1) solid $text-color;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  @include mq('tab'){
    font-size: rem(16);
  }
}

// 次へ
.p-introduction__next a{
  padding: rem(10);
  width: rem(100);
  display: inline-block;
  font-family: $font-family-sub;
  font-size: rem(14);
  line-height: 1;
  letter-spacing: 0.02em;
  border-bottom: rem(1) solid $text-color;
  text-align: left;
  text-transform: uppercase;
  position: relative;

  @include mq('tab'){
    width: rem(130);
    font-size: rem(16);
  }
}

.p-introduction__next a::after{
  content: '';
  display: inline-block;
  width: rem(7);
  height: rem(7);
  border-bottom: rem(2) solid $text-color;
  border-right: rem(2) solid $text-color;
  position: absolute;
  top: 50%;
  right: rem(10);

  transform-origin: center;
  transform: rotate(-45deg) translate(0, -50%);
  transition: right .3s;
}

.p-introduction__next a:hover::after{
  right: rem(5);
}
