}

.swiper-mv .slide-img {
  height: inherit;
}

.swiper-mv .swiper-slide .slide-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.swiper-slide-active .slide-img,
.swiper-slide-duplicate-active .slide-img,
