  height: 1.25rem;
}

.c-hamburger-btn span {
  width: 100%;
  height: 0.125rem;
  background-color: #fff;
  position: absolute;
  transition: all .2s;
}

.c-hamburger-btn span:nth-of-type(1) {
  top: 0;
  left: 0;
  transform-origin: 100% 0;
  transition-delay: .3s;
}

.c-hamburger-btn span:nth-of-type(2) {
  top: 50%;
  left: 0;
  transform: scaleX(1) translateY(-50%);
  transform-origin: 100% 0;
  transition-delay: .3s;
}

.c-hamburger-btn span:nth-of-type(3) {
  bottom: 0;
  left: 0;
  transform-origin: 100% 0;
  transition-delay: .3s;
}

.c-hamburger-btn::before {
  width: 100%;
  height: 0.125rem;
  background-color: #fff;
  content: '';
  position: absolute;
  top: 0;
  left: 0.34375rem;
  transition: all .2s;
  transform-origin: 0 0;
  transform: rotate(45deg) scaleX(0);
  transition-delay: 0s;
}

.c-hamburger-btn::after {
  width: 100%;
  height: 0.125rem;
  background-color: #fff;
  content: '';
  position: absolute;
  top: 0;
  right: 0.34375rem;
  transition: all .2s;
  transform-origin: 100% 0;
  transform: rotate(-45deg) scaleX(0);
  transition-delay: 0s;
}

.c-hamburger-btn.is-open span:nth-of-type(1) {
  transform: scaleX(0);
  transition-delay: 0s;
  background-color: #282828;
}

.c-hamburger-btn.is-open span:nth-of-type(2) {
  transform: scaleX(0) translateY(-50%);
  transition-delay: 0s;
  background-color: #282828;
}

.c-hamburger-btn.is-open span:nth-of-type(3) {
  transform: scaleX(0);
  transition-delay: 0s;
  background-color: #282828;
}

.c-hamburger-btn.is-open::before {
  transform: rotate(45deg) scaleX(1);
  transition-delay: .38s;
  background-color: #282828;
}

.c-hamburger-btn.is-open::after {
  transform: rotate(-45deg) scaleX(1);
  transition-delay: .38s;
  background-color: #282828;
}

