/******************************************************************

.p-opening オープニングアニメーション

******************************************************************/
.p-opening {
  width: 100%;
  height: 100%;
  background-color: #fff;
  opacity: 1;
  visibility: visible;
}

.p-opening.is-fade {
  animation-name: fade;
  animation-duration: 1.5s;
  animation-fill-mode: forwards;
}

.p-opening__box {
  width: 100%;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.p-opening__title {
  overflow: hidden;
}

.p-opening__title.is-upFade {
  animation-name: upFade;
  animation-duration: 1.5s;
  animation-fill-mode: both;
}

.p-opening__title .char {
  display: inline-block;
  font-family: "Oswald", sans-serif;
  font-weight: bold;
  font-size: 1.875rem;
  line-height: 1;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  opacity: 0;
  transform: translateY(50%);
}

.p-opening__title.is-accordion .char {
  animation-name: animation-chars;
  animation-duration: .8s;
  animation-timing-function: cubic-bezier(0.39, 1.57, 0.58, 1);
  animation-fill-mode: both;
}

.p-opening__title.is-accordion .char:nth-child(1) {
  animation-delay: 0.08s;
}

.p-opening__title.is-accordion .char:nth-child(2) {
  animation-delay: 0.16s;
}

